import { LocaleContext, useStyles } from '../../../../lib'
import { useLocale } from '../../../../lib/i18n/LocaleContext'
import ptBr from '../../../../lib/i18n/locales/pt-BR'

function LocaleProviderDemo() {
  return (
    <LocaleContext.Provider value={ptBr}>
      <App />
    </LocaleContext.Provider>
  )
}

function App() {
  const locale = useLocale()
  const { css } = useStyles()

  return (
    <div className={css({ overflow: 'auto', height: '250px' })}>
      <pre>{JSON.stringify(locale)}</pre>
    </div>
  )
}

export default LocaleProviderDemo
